.user-edit-dialog {
  display: flex;
  flex-direction: column;
  min-width: 500px;
  max-width: 600px;

  .dialog-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 24px;
    background: linear-gradient(135deg, #ff6f00 0%, #ff9800 100%);
    color: white;

    .header-content {
      display: flex;
      align-items: center;
      gap: 12px;

      .header-icon {
        font-size: 28px;
        width: 28px;
        height: 28px;
      }

      h2 {
        margin: 0;
        font-size: 20px;
        font-weight: 600;
      }
    }

    .close-btn {
      color: white;
      
      mat-icon {
        font-size: 24px;
        width: 24px;
        height: 24px;
      }
    }
  }

  .dialog-content {
    padding: 24px;
    max-height: 70vh;
    overflow-y: auto;

    .user-info-banner {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 12px 16px;
      background: #fff3cd;
      border: 1px solid #ffc107;
      border-radius: 8px;
      margin-bottom: 24px;
      color: #856404;

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
        color: #ffc107;
      }

      span {
        font-size: 14px;
        font-weight: 500;
      }
    }

    .edit-form {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .full-width {
        width: 100%;
      }

      .role-option {
        display: flex;
        align-items: center;
        gap: 8px;

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
          opacity: 0.7;
        }
      }
    }
  }

  .dialog-actions {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    padding: 16px 24px;
    background: #f5f7fa;

    button {
      min-width: 100px;

      mat-icon {
        margin-right: 6px;
        font-size: 18px;
        width: 18px;
        height: 18px;
      }
    }

    .cancel-btn {
      color: #6b7280;
    }

    .save-btn {
      background: linear-gradient(135deg, #ff6f00 0%, #ff9800 100%);
      color: white;
      font-weight: 600;

      &:not(:disabled):hover {
        background: linear-gradient(135deg, #f57c00 0%, #fb8c00 100%);
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(255, 111, 0, 0.4);
      }

      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }
    }
  }
}

// 반응형
@media (max-width: 768px) {
  .user-edit-dialog {
    min-width: 100%;
    max-width: 100%;

    .dialog-header {
      padding: 16px 20px;

      .header-content {
        h2 {
          font-size: 18px;
        }
      }
    }

    .dialog-content {
      padding: 20px;

      .user-info-banner {
        font-size: 13px;
      }
    }

    .dialog-actions {
      padding: 12px 20px;

      button {
        min-width: 80px;
      }
    }
  }
}
